	    phone: /^1\d{10}$|^(0\d{2,3}-?|\(0\d{2,3}\))?[1-9]\d{4,9}(-\d{1,8})?$|^[48]00-([0-9]){1}([0-9-]{6})([0-9]){1}$/,
