	    ipwithStar: /^(\d|[1-9]\d|[0-1]\d{2}|2[0-4]\d|25[0-5])\.(\d|[1-9]\d|[0-1]\d{2}|2[0-4]\d|25[0-5])\.(\d|[1-9]\d|[0-1]\d{2}|2[0-4]\d|25[0-5]|\*)\.(\d|[1-9]\d|[0-1]\d{2}|2[0-4]\d|25[0-5]|\*)$/,
	    zipcode: /^[0-9]\d{5}$/,
	    qq: /^[1-9]\d{4,10}$/,
	    msn: /^\w+([-+.]\w+)*@\w+([-.]\w+)*\.\w+([-.]\w+)*$/,
